.container-menu-component{
    margin: 2%;
    width: 100%;
}

.type-div{
    padding: 40px;
    width: 100%;
    border-bottom: 1px solid #c9c9c9;
    margin-bottom: 60px;
}

.title-menu {
    margin: 1em 0 1em 0;
    font-size: 70px;
    font-family: 'Castoro', serif;
    color: #bf3757;
    text-align: center;
}

.subtitle{
    font-family: 'Castoro', serif;
    color: #2f2b52;
    text-align: center;
    font-size: 36px;
    margin: 0 0 1.5em 0;
}

@media (max-width: 999px){
    .title-menu {
        margin: 1em 0 1em 0;
        font-size: 50px;
        font-family: 'Castoro', serif;
        color: #bf3757;
    }
}

@media(min-width: 1000px){
    .fancy {
        line-height: 0.5;
        text-align: center;
    }
    
    .fancy span {
        display: inline-block;
        position: relative;  
    }
    
    .fancy span:before,
    .fancy span:after {
        content: "";
        position: absolute;
        height: 16px;
        border-bottom: 1px solid #404040;
        top: 0;
        width: 600px;
    }
    
    .fancy span:before {
        right: 100%;
        margin-right: 15px;
    }
    
    .fancy span:after {
        left: 100%;
        margin-left: 15px;
    }
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap');
.item-title{
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    color: #2f2b52;
    overflow :hidden;
    text-align: left;
}

.right{
    float: right;
}

.right-additions{
    float: right;
    font-size: 14px !important;
}

.item-div{
    margin: 20px;
}

.item-description{
    margin-top: 16px;
    font-family: 'Montserrat', sans-serif;
    color: #2f2b52;
    width: 94%;
}

.additions{
    margin-top: 16px;
    font-family: 'Montserrat', sans-serif;
    color: #2f2b52;
}
