@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);

.title{
    margin-top: 40%;
    text-align: center;
    font-family: 'Castoro', serif;
    font-size: 60px;
}
.title-2{
    font-family: 'Amatic SC', cursive;
    color: black;
}


.homepage{
    margin: 0px;
}

.fondo1{
    height: 800px;
    background-image: url(/static/media/fondo1.5393c60f.png);
    background-repeat: no-repeat;
    background-position-x: -22em;
}

.fondo2{
    height: 800px;
    background-image: url(/static/media/fondo2.0b0a5772.png);
    background-repeat: no-repeat;
    background-position-x: 16em;
    background-position-y: -10em;
}

.fondo3{
    height: 800px;
    background-image: url(/static/media/fondo3.e5f9b142.png);
    background-repeat: no-repeat;
    background-position-x: 0em;
}

@media (max-width: 760px){
    .fondo1, .fondo2{
        display: none;
    }
}

.col-about{
    padding: 6%;
}

.about-info{
    font-size: 28px;
    font-family: 'Montserrat', sans-serif;
    color: #2f2b52;
}

.personal-info{
    font-size: 22px;
    font-family: 'Montserrat', sans-serif;
    color:#4a4a4a;
}

.social{
    font-size: 26px;
    font-family: 'Montserrat', sans-serif;
    color: #bf3757;
}

.link-about{
    font-family: 'Amatic SC', cursive;
    font-size: 40px;
    color: black;
}

.link-about:hover{
    text-decoration: none;
    color: #4a4a4a;
}
.container-menu-component{
    margin: 2%;
    width: 100%;
}

.type-div{
    padding: 40px;
    width: 100%;
    border-bottom: 1px solid #c9c9c9;
    margin-bottom: 60px;
}

.title-menu {
    margin: 1em 0 1em 0;
    font-size: 70px;
    font-family: 'Castoro', serif;
    color: #bf3757;
    text-align: center;
}

.subtitle{
    font-family: 'Castoro', serif;
    color: #2f2b52;
    text-align: center;
    font-size: 36px;
    margin: 0 0 1.5em 0;
}

@media (max-width: 999px){
    .title-menu {
        margin: 1em 0 1em 0;
        font-size: 50px;
        font-family: 'Castoro', serif;
        color: #bf3757;
    }
}

@media(min-width: 1000px){
    .fancy {
        line-height: 0.5;
        text-align: center;
    }
    
    .fancy span {
        display: inline-block;
        position: relative;  
    }
    
    .fancy span:before,
    .fancy span:after {
        content: "";
        position: absolute;
        height: 16px;
        border-bottom: 1px solid #404040;
        top: 0;
        width: 600px;
    }
    
    .fancy span:before {
        right: 100%;
        margin-right: 15px;
    }
    
    .fancy span:after {
        left: 100%;
        margin-left: 15px;
    }
}
.item-title{
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    color: #2f2b52;
    overflow :hidden;
    text-align: left;
}

.right{
    float: right;
}

.right-additions{
    float: right;
    font-size: 14px !important;
}

.item-div{
    margin: 20px;
}

.item-description{
    margin-top: 16px;
    font-family: 'Montserrat', sans-serif;
    color: #2f2b52;
    width: 94%;
}

.additions{
    margin-top: 16px;
    font-family: 'Montserrat', sans-serif;
    color: #2f2b52;
}

.container-menu-page{
    background-color: #f5f8fa;
    margin-top: 0px!important;
}

@media(max-width: 760px){
    .container-menu-page{
        padding: 0px;
    }
}
.notification{
    position: fixed;
    right: 20px;
    bottom: 0;
    transform: 1s;
    background-color: #bf3757;
    color: white;
    border: 1px solid black;
    font-family: 'Major Mono Display', monospace;
    font-size: 16px;
}

.label-reservation{
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    color: #2f2b52;
}

.form-control-reservation:focus{
    border: 1px solid grey;
    border-bottom: 2px solid #bf3757;
    box-shadow: none;
}

.text-reservation{
    font-family: 'Montserrat', sans-serif;
    color: grey;
}

.phone-number{
    color: #bf3757;
}

.button-reservation{
    background: transparent;
    border: 1px solid #bf3757;
    color: #bf3757;
    font-size: 22px;
}

.button-reservation:hover{
    background: #bf3757;
    color: white;
    border: none;
}

.reservation-container{
    margin-top: 4%;
}

.row-form{
    margin-top: 4%;
}

.btn-middle{
    margin-top: 40px;
    text-align: center;
}

.personal-info2{
    margin-top: 6%;
}

@media (max-width: 760px){
    .reservation-container{
        margin-bottom: 100px;
    }
}
#responsive-navbar-nav{
    text-align: center;
}

.nav-link{
    font-family: 'Major Mono Display', monospace;
    margin-top: 20px;
    color: black !important;
}

.logo{
    font-size: 40px;
}

.link-style{
    font-size: 24px;
}

.link-style:hover{
    transition: 0.5s;
    text-decoration:line-through;
}

.restaurant{
    color: #bf3757;
}

@media (max-width: 700px){
    .logo{
        font-size: 30px;
    }
}
