.label-reservation{
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    color: #2f2b52;
}

.form-control-reservation:focus{
    border: 1px solid grey;
    border-bottom: 2px solid #bf3757;
    box-shadow: none;
}

.text-reservation{
    font-family: 'Montserrat', sans-serif;
    color: grey;
}

.phone-number{
    color: #bf3757;
}

.button-reservation{
    background: transparent;
    border: 1px solid #bf3757;
    color: #bf3757;
    font-size: 22px;
}

.button-reservation:hover{
    background: #bf3757;
    color: white;
    border: none;
}

.reservation-container{
    margin-top: 4%;
}

.row-form{
    margin-top: 4%;
}

.btn-middle{
    margin-top: 40px;
    text-align: center;
}

.personal-info2{
    margin-top: 6%;
}

@media (max-width: 760px){
    .reservation-container{
        margin-bottom: 100px;
    }
}